@font-face {
  font-family: "FiraSans";
  font-weight: 400;
  font-style: normal;
  src: local("FiraSans-regular"), url("../fonts/Firasans-regular.woff2") format("woff2"), url("../fonts/Firasans-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "FiraSans";
  font-weight: 500;
  font-style: normal;
  src: local("FiraSans-medium"), url("../fonts/Firasans-medium.woff2") format("woff2"), url("../fonts/Firasans-medium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "FiraSans";
  font-weight: 700;
  font-style: normal;
  src: local("FiraSans-bold"), url("../fonts/Firasans-bold.woff2") format("woff2"), url("../fonts/Firasans-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  src: local("Montserrat-bold"), url("../fonts/Montserrat-bold.woff2") format("woff2"), url("../fonts/Montserrat-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  font-weight: 800;
  font-style: normal;
  src: local("Raleway-extrabold"), url("../fonts/Raleway-extrabold.woff2") format("woff2"), url("../fonts/Raleway-extrabold.woff") format("woff");
  font-display: swap; }

.wrapper {
  width: 1366px;
  margin: 0 auto;
  padding: 0 2%;
  box-sizing: border-box; }
  @media (min-width: 1024px) and (max-width: 1365px) {
    .wrapper {
      width: 1024px;
      padding: 0 15px; } }
  @media (min-width: 768px) and (max-width: 1023px) {
    .wrapper {
      width: 768px;
      padding: 0 30px; } }
  @media (min-width: 320px) and (max-width: 767px) {
    .wrapper {
      width: 320px;
      padding: 0 16px; } }

.main-container {
  background-image: url("../img/promo-bg.jpg"), url("../img/promo-photo.png");
  background-repeat: no-repeat, no-repeat;
  background-position: bottom, bottom;
  background-size: cover, auto;
  background-color: #ce9b9b; }

.header {
  padding: 32px 0 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px; }

.header .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__phone {
  text-decoration: none;
  color: #484848; }

.header__callback {
  text-decoration: none;
  color: #484848;
  border-bottom: 1px dashed #000000;
  font-variant: small-caps; }

@media (min-width: 320px) and (max-width: 767px) {
  .header {
    font-size: 14px;
    line-height: 17px;
    padding-top: 20px; }
  .header .wrapper {
    flex-wrap: wrap;
    flex-direction: column;
    align-content: flex-end;
    position: relative; }
  .header__logo {
    position: absolute;
    order: -1;
    left: 15px;
    top: 0; }
  .header__phone {
    order: 0; }
  .header__callback {
    order: 1;
    align-self: flex-end;
    margin-top: 8px; } }

.promo__title {
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 900;
  font-size: 200px;
  line-height: 170%;
  letter-spacing: 0.055em;
  text-transform: uppercase;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  text-align: center;
  margin: 70px 0 0; }

.promo__description {
  width: 363px;
  margin: 38px auto 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #FFFFFF;
  text-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12); }

.promo__scrolling {
  margin: 32px auto 0;
  text-align: center; }

@media (min-width: 1024px) and (max-width: 1365px) {
  .promo__title {
    font-size: 90px; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .promo__description {
    font-size: 20px;
    line-height: 24px; }
  .promo__title {
    font-size: 80px; } }

@media (min-width: 320px) and (max-width: 767px) {
  .promo__title {
    font-size: 72px; } }

.logo {
  font-size: 0; }

.title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 49px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #1FC0FC; }

@media (min-width: 768px) and (max-width: 1023px) {
  .title {
    font-size: 32px;
    line-height: 39px; } }

.about {
  background-image: url("../../img/about-color_bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #ce9b9b; }

.about__title {
  text-align: center;
  margin-top: 0; }

.about__text {
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 170%;
  text-align: center;
  color: #484848;
  width: 700px;
  margin: 24px auto 0; }

@media (min-width: 320px) and (max-width: 767px) {
  .about__text {
    width: 100%;
    font-size: 16px;
    line-height: 160%; } }
